body {
    text-align: center;
}

.main-app {
    background-color: #f1dfd1;
    background-image: linear-gradient(315deg, #f1dfd1 0%, #f6f0ea 74%);
    width: 700px;
    height: 80%;
    text-align: center;
    margin: auto;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
}

.main-app:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 14px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 48rem) {
    .main-app {
        background-color: snow;
        width: 80%;
        height: 80vh;
        margin: auto;
        text-align: center;
        box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19);
    }
}